import { SelectionModel } from "@angular/cdk/collections";
import { HttpParams } from "@angular/common/http";
import { Component, Inject, OnInit, ViewChild } from "@angular/core";
import {
  FormGroup,
  FormBuilder,
  Validators,
  AbstractControl,
  FormArray,
} from "@angular/forms";
import {
  MatDialogRef,
  MAT_DIALOG_DATA,
  MatDialog,
  MatDialogConfig,
} from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { Subject, takeUntil, Subscription, BehaviorSubject } from "rxjs";
import { GlsLookupComponent } from "src/app/erp-finance/data/lookups/gls-lookup/gls-lookup.component";
import { TransactionsService } from "src/app/erp-finance/data/services/transactions.service";
import { PurchaseOrderAcknowledgementComponent } from "src/app/erp-procurement/modules/requisition/pages/purchase-order-acknowledgement/purchase-order-acknowledgement.component";
import {
  FilesService,
  SelectedFiles,
} from "src/app/shared/services/files.service";
import { SnackbarService } from "src/app/shared/services/snackbar.service";

import * as XLSX from "xlsx";

@Component({
  selector: "app-accounting-journal-transactions",
  templateUrl: "./accounting-journal-transactions.component.html",
  styleUrls: ["./accounting-journal-transactions.component.sass"],
})
export class AccountingJournalTransactionsComponent implements OnInit {
  mngForm: FormGroup;
  tranForm: FormGroup;
  hide3 = true;
  agree3 = false;

  displayedColumns: string[] = [
    "id",
    "drAcName",
    "drAcId",
    "drAcCurrencyCode",
    "crAcName",
    "crAcId",
    "crAcCurrencyCode",
    "amount",
    "narration",
    "action",
  ];

  dataSource = new MatTableDataSource<any>([]);
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  selection = new SelectionModel<any>(false, []);

  destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;

  pageFunction: string = "Add";

  hideSubmit: boolean = false;

  tranTypes: any[] = [{ name: "Debit" }, { name: "Credit" }];

  transactionTypes: any[] = [
    { name: "Receiving" },
    { name: "Inventory" },
    { name: "FixedAssets" },
    { name: "SupplierInvoice" },
    { name: "CustomerInvoice" },
    { name: "SupplierPayment" },
    { name: "CustomerPayment" },
    { name: "Standard" },
    { name: "ImprestApplication" },
    { name: "ImprestClearance" },
    { name: "Salaries" },
    { name: "Journals" },
  ];
  imprestAccountingStatuses: any[] = [
    { name: "Full" },
    { name: "Partial" },
    { name: "Return" },
  ];

  constructor(
    public dialogRef: MatDialogRef<PurchaseOrderAcknowledgementComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private fb: FormBuilder,
    private snackbar: SnackbarService,
    private transactionService: TransactionsService,
    private dialog: MatDialog,
    private filesService: FilesService
  ) { }

  ngOnInit(): void {
    console.log("LOG DATA = ", this.data);
    this.pageFunction = this.data.action;
    this.selection = new SelectionModel<any>(true, []);

    this.generateForm();
    this.generateTransactionForm();

    this.getPage();
  }
  ngAfterViewInit() {
    if (this.data.action !== "Add") {
      this.generateForm(this.formData);
      this.refresh();
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  disableTransactionType: boolean = true;
  getPage() {
    this.disableTransactionType = true;

    if (this.data.action === "Add" || this.data.action === "Preview") {
      this.isLoading = false;
      if (
        this.data.transactionType === "Receiving" ||
        this.data.transactionType === "Invoice" ||
        this.data.transactionType === "Payment" ||
        this.data.transactionType === "Inventory" ||
        this.data.transactionType === "CustomerInvoice" ||
        this.data.transactionType === "SupplierInvoice" ||
        this.data.transactionType === "SupplierPayment" ||
        this.data.transactionType === "CustomerPayment" ||
        this.data.transactionType === "Standard" ||
        this.data.transactionType === "Journals" ||
        this.data.transactionType === "ImprestApplication" ||
        this.data.transactionType === "ImprestClearance" ||
        this.data.transactionType === "Salaries"
      ) {
        console.log("this.data.bodyForAdd:: ", this.data.bodyForAdd);

        this.generateForm(this.data.bodyForAdd);
        this.dataSource.data = this.data.bodyForAdd.parttrans;
        this.refresh();

        this.mngForm.patchValue({
          paymentattachements: [],
        });
      }
      this.mngForm.patchValue({
        paymentattachements: [],
      });
    } else if (this.data.action === "Update") {
      this.getDataByParameters();
    } else if (this.data.action === "View") {
      this.getDataByParameters();
      this.activateViewMode();
    } else if (this.data.action === "View Salary") {
      this.isLoading = false;
      if (this.data.transactionType === "Salaries") {
        console.log("this.data.bodyForAdd: ", this.data.bodyForAdd);

        this.formData = this.data.bodyForAdd;

        this.generateForm(this.formData);
        this.dataSource.data = this.data.bodyForAdd.parttrans;
        this.refresh();
      }
    }
  }

  formData: any;
  getDataByParameters() {
    this.isLoading = true;
    const params = new HttpParams().set("tranCode", this.data.tranCode);
    this.transactionService
      .getTransactionsByTranCode(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("res :", res);
          if (res.entity) {
            this.formData = res.entity;
            if (
              this.formData &&
              this.formData.paymentattachements &&
              this.formData.paymentattachements.length > 0
            ) {
              this.isFileDataLoading = false;
              this.formData.paymentattachements.forEach((element) => {
                const newRow = this.fb.group({
                  file: [element.file],
                  fileName: [element.fileName],
                  fileType: [element.fileType],
                });
                this.rows.push(newRow);
              });
              this.updateView();
            }

            this.generateForm(this.formData);
            this.dataSource.data = this.formData.parttrans;
            this.refresh();

            console.log("this.formData :", this.formData);
          } else {
            this.snackbar.showNotification("snackbar-danger", res.message);
          }
        },
        error: (err) => {
          this.snackbar.showNotification("snackbar-danger", err.message);
        },
        complete: () => {
          this.isLoading = false;
        },
      }),
      Subscription;
  }

  generateForm(formData?): void {
    console.log("generateForm formData :: ", formData);
    this.mngForm = this.fb.group({
      id: [formData?.id ?? ""],
      description: [formData?.description ?? "", [Validators.required]],
      itemCode: [formData?.itemCode ?? ""],
      referenceCode: [formData?.referenceCode ?? ""],
      transactionType: [formData?.transactionType ?? "", [Validators.required]],
      prCode: [formData?.prCode ?? ""],
      imprestAccountingStatus: [formData?.imprestAccountingStatus ?? ""],
      parttrans: [formData?.parttrans ?? "", [Validators.required]],
      paymentattachements: [[]],
    });

    if (
      formData &&
      formData.paymentattachements &&
      formData.paymentattachements.length > 0
    ) {
      this.mngForm.patchValue({
        paymentattachements: formData.paymentattachements,
      });
    }
  }

  generateTransactionForm(formData?): void {
    this.tranForm = this.fb.group({
      id: [formData?.id ?? ""],
      drAcCurrencyCode: [
        formData?.drAcCurrencyCode ?? "",
        [Validators.required],
      ],
      drAcName: [formData?.drAcName ?? "", [Validators.required]],
      drAcId: [formData?.drAcId ?? "", [Validators.required]],

      crAcCurrencyCode: [
        formData?.crAcCurrencyCode ?? "",
        [Validators.required],
      ],
      crAcName: [formData?.crAcName ?? "", [Validators.required]],
      crAcId: [formData?.crAcId ?? "", [Validators.required]],

      amount: [
        formData?.amount ?? 0,
        [
          Validators.required,
          Validators.max(1000000000),
          Validators.pattern(/^\d+(\.\d{1,2})?$/),
          Validators.min(0),
        ],
      ],
      narration: [formData?.narration ?? "", [Validators.required]],
    });
  }

  viewMode: boolean = false;
  activateViewMode() {
    this.viewMode = true;
  }

  //*********************************************************************************************************************************************************** */
  // Form&Table Logic
  //*********************************************************************************************************************************************************** */
  isLoading: boolean = true;
  showParTranForm = false;
  parTranAction: string = "";
  selectedParTranIndex: number;
  debitTotal: number = 0;
  creditTotal: number = 0;
  balance: number = 0;

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  resetFormAndHide() {
    this.mngForm.patchValue({
      parttrans: this.dataSource.data,
    });
    this.tranForm.reset();
    this.showParTranForm = false;
  }

  addParTran() {
    this.parTranAction = "Add";
    this.showParTranForm = true;
    this.showImport = false;
  }

  refresh() {
    this.dataSource.data = [...this.dataSource.data];
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;

    const { debitTotal, creditTotal, balance } = this.calculateTotals();
    this.debitTotal = debitTotal;
    this.creditTotal = creditTotal;
    this.balance = balance;
  }

  editParTran(data: any, index: number) {
    this.parTranAction = "Update";
    this.selectedParTranIndex = index;
    this.generateTransactionForm(data);
    this.showParTranForm = true;
    this.showImport = false;
  }

  pushToDataSource() {
    if (this.parTranAction === "Add") {
      this.dataSource.data.push(this.tranForm.value);
    } else if (this.parTranAction === "Update") {
      let indexToUpdate: number;

      if (this.tranForm.value.id) {
        indexToUpdate = this.dataSource.data.findIndex(
          (item) => item.id === this.tranForm.value.id
        );
      } else {
        indexToUpdate = this.selectedParTranIndex;
      }

      if (indexToUpdate !== -1) {
        this.dataSource.data[indexToUpdate] = this.tranForm.value;
      }
    }

    this.resetFormAndHide();
    this.refresh();
  }
  pushBulkToDataSource() {
    this.dataSource.data.push(...this.items);

    this.resetFormAndHide();
    this.refresh();
  }

  deleteParTran(index: number) {
    this.dataSource.data.splice(index, 1);
    this.refresh();
  }

  cancelParTran() {
    this.resetFormAndHide();
  }

  calculateTotals(): {
    debitTotal: number;
    creditTotal: number;
    balance: number;
  } {
    let debitTotal = 0;
    let creditTotal = 0;

    this.dataSource.data.forEach((element) => {
      if (element.partTranstype === "Debit") {
        debitTotal += element.amount;
      } else if (element.partTranstype === "Credit") {
        creditTotal += element.amount;
      }
    });

    const balance = creditTotal - debitTotal;

    return { debitTotal, creditTotal, balance };
  }

  //******************************************************************************************************************************************************** */
  // Look ups
  selectedItem: any[] = [];
  glsLookUp(accType: any) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.disableClose = true;
    dialogConfig.width = "700px";
    dialogConfig.data = {
      action: "Single GL Account",
      selected: this.selectedItem,
    };
    const dialogRef = this.dialog.open(GlsLookupComponent, dialogConfig);
    dialogRef.afterClosed().subscribe((result) => {
      console.log("result:::", result);
      result.data.forEach((element) => {
        switch (accType) {
          case "drAcId":
            this.tranForm.patchValue({
              drAcId: element.accountID,
              drAcName: element.description,
              drAcCurrencyCode: element.currencyID,
            });
            break;

          case "crAcId":
            this.tranForm.patchValue({
              crAcId: element.accountID,
              crAcName: element.description,
              crAcCurrencyCode: element.currencyID,
            });
            break;
          default:
            break;
        }
      });
    });
  }

  // **********************************************************************************************************************************************************
  //UploadDocuments

  attachementsDataSource = new BehaviorSubject<AbstractControl[]>([]);
  displayColumns = [
    "id",
    "fileName",
    "fileType",
    "selectFile",
    "download",
    "action",
  ];
  rows: FormArray = this.fb.array([]);
  documentsForm: FormGroup = this.fb.group({ filedetails: this.rows });

  isFileDataLoading: boolean = true;

  fileForm: FormGroup;
  fileInputs: FormArray;

  createFileForm() {
    this.fileForm = this.fb.group({
      files: this.fb.array([]),
    });
    //this.fileInputs = this.fileForm.get('files') as FormArray;
    this.addFileRow();
  }

  addFileRow() {
    this.isFileDataLoading = false;
    const row = this.fb.group({
      file: ["", Validators.required],
      fileType: ["", Validators.required],
      fileName: ["", Validators.required],
    });
    //this.fileInputs.push(row);
    this.rows.push(row);
    this.attachementsDataSource.next(this.rows.controls);
  }

  updateView() {
    this.attachementsDataSource.next(this.rows.controls);
    this.mngForm.patchValue({
      paymentattachements: this.documentsForm.value.filedetails,
    });
  }

  deleteCall(row: AbstractControl) {
    const dataArray = this.attachementsDataSource.getValue(); // Get the underlying array
    const index = dataArray.indexOf(row); // Find the index of the row

    if (index !== -1) {
      dataArray.splice(index, 1); // Remove 1 item at the specified index
      this.attachementsDataSource.next(dataArray); // Update the BehaviorSubject with the modified array
    }
  }

  public selectedFiles: SelectedFiles[] = [];
  public isFileLoading = new BehaviorSubject(false);

  currFile: string;
  currfileName: any;
  currType: any;

  onSelectFile(files, selectedRow, index) {
    console.log("row: ", selectedRow.value, index);
    this.isFileLoading.next(true);
    this.selectedFiles = [];
    this.filesService.toBase64(files, this.selectedFiles).subscribe((res) => {
      if (res) {
        this.isFileLoading.next(false);

        this.selectedFiles = res;
        console.log("selectedFiles: ", this.selectedFiles);

        console.log("this.selectedFiles[0].name: ", this.selectedFiles[0].name);

        this.currFile = this.selectedFiles[0].base64;

        const fileName = this.selectedFiles[0].name;
        const fileParts = fileName.split(".");
        const name = fileParts.slice(0, -1).join("."); // Join all parts except the last one
        const extension = fileParts[fileParts.length - 1];

        console.log("Name:", name); // Output: Lease Armotization (3)
        console.log("Extension:", extension); // Output: xlsx

        this.currfileName = name;
        this.currType = extension;

        this.rows.at(index).patchValue({
          file: this.currFile,
          fileName: this.currfileName,
          fileType: this.currType,
        });
        this.updateView();
      }
    });
  }

  downloadDocument(row: any) {
    let file = row.value.file;
    let fileName = row.value.fileName;
    let fileType = row.value.fileType;

    // Download PDF in Chrome etc.
    const source = `${file}`;
    const link = document.createElement("a");
    link.href = source;
    link.download = `${fileName}.${fileType}`;
    link.click();
  }

  //*************************************************************************************************** */

  posting: boolean = false;
  cleanUpIds(formData: any): any {
    const cleanRecursive = (data: any): any => {
      if (data === null || typeof data !== "object") {
        return data;
      }
      if (Array.isArray(data)) {
        // If it's an array, recursively clean each element
        return data.map((element) => cleanRecursive(element));
      }

      // Remove "id" field if it is null, undefined, or an empty string
      const cleanedData: any = {};
      for (const [key, value] of Object.entries(data)) {
        if (
          key === "id" &&
          (value === null || value === undefined || value === "")
        ) {
          continue; // Skip "id" field
        }
        cleanedData[key] = cleanRecursive(value);
      }
      return cleanedData;
    };
    // Clone the form data to avoid modifying the original object
    const cleanedData = cleanRecursive({ ...formData });

    return cleanedData;
  }

  response: any;
  submit() {
    const cleanedFormData = this.cleanUpIds(this.mngForm.value);

    console.log("Both: ", cleanedFormData);
    this.posting = true;
    if (this.pageFunction === "Add") {
      this.transactionService
        .addTransactions(cleanedFormData)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            this.response = res;
            console.log("res: ", res);
            if (res.statusCode == 201 || 200 || 302) {
              this.snackbar.showNotification("snackbar-success", res.message);
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
          },
          complete: () => {
            this.posting = false;
            this.onNoClick();
          },
        }),
        Subscription;
    } else if (this.pageFunction === "Update") {
      this.transactionService
        .updateTransactions(cleanedFormData)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            this.response = res;
            console.log("resres:: ", res);
            if (res.statusCode == 201 || 200 || 302) {
              this.snackbar.showNotification("snackbar-success", res.message);
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
          },
          complete: () => {
            this.posting = false;
            this.onNoClick();
          },
        }),
        Subscription;
    }
  }

  // *********************************************************************************************************************************************
  showImport: boolean = false;
  import() {
    this.showImport = true;
    this.showParTranForm = false;
  }
  //Import from Excel
  @ViewChild("fileInput") fileInput: any;

  items: any[] = []; // Array to store
  itemsForm: FormGroup;
  itemErrors: { [key: string]: string }[] = [];

  handleFileInput(files: FileList) {
    const file = files.item(0);

    if (file) {
      const fileReader = new FileReader();
      fileReader.onload = (e: any) => {
        const arrayBuffer = e.target.result;
        const workbook = XLSX.read(arrayBuffer, { type: "array" });
        const worksheet = workbook.Sheets[workbook.SheetNames[0]];
        const jsonData = XLSX.utils.sheet_to_json(worksheet, { raw: true });

        // Trim the keys in each object of jsonData
        const trimmedData = jsonData.map((item: any) => {
          const trimmedItem: any = {};
          Object.keys(item).forEach((key) => {
            trimmedItem[key.trim()] = item[key];
          });
          return trimmedItem;
        });

        this.items = trimmedData;
        this.pushBulkToDataSource();
        console.log(" this.items :", this.items);

        // Initialize the form
        this.itemsForm = this.fb.group({
          items: this.fb.array([]), // Array of items
        });

        // Call the function to validate the items
        this.validateItems();
      };

      fileReader.readAsArrayBuffer(file);
    }
  }

  validateItems() {
    this.itemErrors = [];
    const items = this.items;

    const itemsFormArray = this.itemsForm.get("items") as FormArray;

    for (const item of items) {
      const formGroup = this.fb.group({
        drAcCurrencyCode: [item.drAcCurrencyCode, [Validators.required]],
        drAcName: [item.drAcName, [Validators.required]],
        drAcId: [item.drAcId, [Validators.required]],
        crAcCurrencyCode: [item.crAcCurrencyCode, [Validators.required]],
        crAcName: [item.crAcName, [Validators.required]],
        crAcId: [item.crAcId, [Validators.required]],

        amount: [
          item.amount,
          [
            Validators.required,
            Validators.max(1000000000),
            Validators.pattern(/^\d+(\.\d{1,2})?$/),
            Validators.min(0),
          ],
        ],
        narration: [item.narration],
      });

      itemsFormArray.push(formGroup);
      this.collectErrors(formGroup);

      if (formGroup.valid) {
      }
    }
  }

  collectErrors(control: AbstractControl, path: string = "") {
    if (control instanceof FormGroup) {
      Object.keys(control.controls).forEach((key) => {
        const subControl = control.get(key);
        const subPath = this.getErrorPath(path, key);
        this.collectErrors(subControl, subPath);
      });
    } else {
      const errors = control.errors;
      if (errors) {
        const itemError = {
          field: path,
          message: this.getErrorMessage(errors),
        };
        this.itemErrors.push(itemError);
      }
    }
  }

  getErrorPath(path: string, key: string): string {
    return path ? `${path}.${key}` : key;
  }

  getErrorMessage(errors: any): string {
    if (errors.required) {
      return "This field is required.";
    } else if (errors.pattern) {
      return "Invalid value.";
    }
    return "";
  }

  private readonly templateFilename = "JournalsTemplate.xlsx";

  downloadTemplate() {
    const link = document.createElement("a");
    link.setAttribute("type", "hidden");
    link.href = `/assets/templates/${this.templateFilename}`;
    link.download = this.templateFilename;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }

  onNoClick(): void {
    this.dialogRef.close({ event: "close", data: this.response });
  }
}
